// MIXINS

.page-wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 0.5rem;
}

.intro {
  order: 1;
}

.main {
  order: 3;
}

.sidebar {
  order: 2;
}

header,
footer,
.summary,
.preamble,
.explanation,
.participation,
.benefits,
.requirements,
.sidebar {
  border-width: $border-width;
  border-style: solid;
  margin: 1rem 0;
}

@media only screen and (min-width: $M) {
	.page-wrapper {
		margin: 0 5rem;
	}

	.wrapper {
		display: grid;
		grid-template-columns: 1fr 1fr;
	}

	.design-selection {
		grid-row: span 2;
		border-right-width: $border-width;
		border-right-style: solid;
		align-self: stretch; 
	}

	.zen-resources {
		grid-column: 2 / 3;
	}
}

@mixin boxes {
	position: fixed;
	width: $console-width;
}

@media only screen and (min-width: $L) {
  header,
  footer,
  .summary,
  .preamble,
  .explanation,
  .participation,
  .benefits,
  .requirements,
  .sidebar {
    margin: 0;
  }

  .intro {
    position: fixed;
    top: 0;
    left: 50vw;
    transform: translate(-50%, 0);
  }

  .sidebar {
    position: fixed;
    left: 0;
    top: 50vh;
    bottom: $console-2nd-row-bottom;
    width: 175px;
    height: 90vh;
    padding: 1rem;
    transform: translate(0, -50%);
    transition: transform 2s ease-in-out;

  }

  .sidebar ul {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  .sidebar h3 {
    position: absolute;
    width: 200px;
    transform: rotate(90deg) translate(50%, -300%);
  }

  .sidebar:hover {
    transform: translate(100px, -50%);
  }

  .main {
    @include boxes;
    bottom: $console-2nd-row-bottom;
    left: $console-row-position;
  }

  .preamble {
    @include boxes;
    top: $console-1st-row-top;
    transform: translate(-33%, 0);
  }

  .explanation {
    @include boxes;
    top: $console-1st-row-top;
    right: $console-row-position;
  }

  .requirements {
    @include boxes;
    bottom: $console-2nd-row-bottom;
    right: $console-row-position;
  }

  .wrapper {
    display: block;
  }

  footer {
    position: fixed;
    bottom: 0;
    left: 50vw;
    transform: translate(-50%, 0);
  }

  .design-selection {
    border: none;
  }
}